.media-content {
    .img-preview {
        width: 350px;
        height: 300px;
        border: 1px solid var(--border-dim);
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        overflow: hidden;
        .img-container {
            background-position: center center;
            height: 100%;
            width: 100%;
            background-size: contain;
            background-repeat: no-repeat;
        }
    }
    .preview-img-title {
        padding: 5px 1rem;
        background-color: var(--hover-color);
    }

    @media (max-width: 480px) {
        .img-preview {
            width: 100%;
            // height: 300px;
        }
    }
}

.primary-post-content {
    .img-preview {
        width: 225px;
        height: 175px;
        border: 1px solid var(--border-dim);
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        flex-shrink: 0;
        overflow: hidden;
        .img-container {
            background-position: center center;
            height: 100%;
            width: 100%;
            background-size: contain;
            background-repeat: no-repeat;
        }
    }
    .pp-content {
    }
}

.cms-post {
    position: relative;
    .bg-max {
        background-color: var(--bg-max);
        border-radius: 10px;
        // height: 700px;
    }
}

.hero-types {
    // display: flex;
    // flex-direction: column;
    // justify-content: center;
    .hero-type {
        padding: 5px 20px;
        background-color: var(--base-color);
        border-radius: 10px;
        color: var(--text-color);
        text-transform: capitalize;
        font-size: var(--fs-md);
        // font-weight: 600;
        // transition: .3s ease-in-out;
        border: 2px solid transparent;
        cursor: pointer;
        .icon {
            vertical-align: text-bottom;
        }
        &:hover {
            background-color: var(--base-light);
        }
        &.active {
            background-color: var(--base-light);
            color: var(--text-color);
            border-color: var(--border-primary);
        }
    }
}

