.custom-fields-container {
    .delete-option {
        position: absolute;
        right: 8px;
        top: -10px;
        cursor: pointer;
        i {
            font-size: 1.5em;
        }
    }

    .cf-attachment {
        // border-color: var(--border-color);
        background-color: transparent;
        border-radius: 5px;
        border: 1px solid var(--border-color);
        min-height: 2.5rem;
        .cf-attachment-img {
            img {
                width: 100%;
            }
        }
        .delete-cf-image {
            position: absolute;
            right: 5px;
            top: 5px;
            cursor: pointer;
            i {
                font-size: 1.5em;
            }
        }
    }

    .cf-image-uploader {
        // background-color: aquamarine;
        width: fit-content;
        input[type="file"] {
            display: none;
        }
        .custom-file-upload {
            display: flex;
            align-items: center;
            justify-content: center;
            cursor: pointer;
            width: 80px;
            height: 80px;
            border-radius: 50%;
            // background: linear-gradient(136.96deg, #546AE4 24.16%, #5A9DF1 59.62%, #60D0FE 85.17%);
            background: white;
            visibility: hidden;
            opacity: 0;
            transition:
                visibility 0s linear 300ms,
                opacity 300ms;
        }
        &:hover {
            .custom-file-upload {
                visibility: visible;
                opacity: 1;
                transition:
                    visibility 0s linear 0s,
                    opacity 300ms;
            }
        }
        // .dismiss-dp {
        //     width: 25px;
